import React from 'react';
import axios from "axios";
import './JokeList.css'
import Joke from "../Joke/Joke";
import {v4 as uuidv4} from 'uuid';
// import {getCities} from "../../firebase";
// import {initializeApp} from "firebase/app";
// import {getFirestore} from 'firebase/firestore/lite';
class JokeList extends React.Component {

    static defaultProps = {
        numJokesToGet: 10
    };
    constructor(props) {
        super(props);
        this.state = {
            jokes: JSON.parse(window.localStorage.getItem("jokes") || "[]"),
            loading: false
        };
        this.seenJokes = new Set(this.state.jokes.map(j => j.joke));
        this.handleClick = this.handleClick.bind(this);
    }
    componentDidMount() {
        if (this.state.jokes.length === 0) this.getJokes();
    }
    async getJokes() {
        try {
            let jokes = [];
            while (jokes.length < this.props.numJokesToGet) {
                let res = await axios.get("https://icanhazdadjoke.com/", {
                    headers: { Accept: "application/json" }
                });
                let newJoke = res.data.joke;
                if (!this.seenJokes.has(newJoke)) {
                    jokes.push({ id: uuidv4(), joke: newJoke, votes: 0 });
                } else {
                    console.log("FOUND A DUPLICATE!");
                    console.log(newJoke);
                }
            }
            this.setState(
                st => ({
                    loading: false,
                    jokes: [ ...jokes,...st.jokes]
                }),
                () =>
                    window.localStorage.setItem("jokes", JSON.stringify(this.state.jokes))
            );
        } catch (e) {
            alert(e);
            this.setState({ loading: false });
        }
    }
    handleVote(id, delta) {
        this.setState(
            st => ({
                jokes: st.jokes.map(j =>
                    j.id === id ? { ...j, votes: j.votes + delta } : j
                )
            }),
            () =>
                window.localStorage.setItem("jokes", JSON.stringify(this.state.jokes))
        );
    }
    handleClick() {
        this.setState({ loading: true }, this.getJokes);
    }
    render() {
        // const firebaseConfig = {
        //     apiKey: "AIzaSyC6RZY6e2IcvN3Uxg7oCU6fYJp5D9CUQ4I",
        //     authDomain: "badjokes-7221f.firebaseapp.com",
        //     projectId: "badjokes-7221f",
        //     storageBucket: "badjokes-7221f.appspot.com",
        //     messagingSenderId: "382897124593",
        //     appId: "1:382897124593:web:077734268bba473b9ae550"
        // };
        //
        // const app = initializeApp(firebaseConfig);
        // const db = getFirestore(app);
        // getCities(db)

        if (this.state.loading) {
            return (
                <div className='JokeList-spinner'>
                    <i className='far fa-8x fa-laugh fa-spin' />
                    <h1 className='JokeList-title'>Loading...</h1>
                </div>
            );
        }
        let jokes = this.state.jokes.sort((a, b) => b.votes - a.votes);
        return (
            <div className='JokeList'>
                <div className='JokeList-sidebar'>
                    <h1 className='JokeList-title'>
                        <span>Bad</span> Jokes
                    </h1>
                    <img src='https://i.ibb.co/CKcZF8V/emoji.png' alt={'d'}/>
                    <button className='JokeList-getmore' onClick={this.handleClick}>
                        Fetch Jokes
                    </button>
                </div>

                <div className='JokeList-jokes'>
                    {jokes.map(j => (
                        <Joke
                            key={j.id}
                            votes={j.votes}
                            joke={j.joke}
                            upvote={() => this.handleVote(j.id, 1)}
                            downvote={() => this.handleVote(j.id, -1)}
                        />
                    ))}
                </div>
            </div>
        );
    }
}
export default JokeList;
