import React from 'react';
import './Joke.css'

class Joke extends React.Component {
    getColor() {
        if (this.props.votes >= 15) {
            return '#4caf50'
        } else if (this.props.votes >= 12) {
            return '#8bc34a'
        } else if (this.props.votes >= 9) {
            return '#cddc39'
        } else if (this.props.votes >= 6) {
            return '#ffeb3b'
        } else if (this.props.votes >= 3) {
            return '#ffc107'
        } else if (this.props.votes >= 0) {
            return '#ff9800'
        } else {
            return '#f44336'
        }
    }

    getEmoji() {
        if (this.props.votes >= 15) {
            return 'em em-rolling_on_the_floor_laughing'
        } else if (this.props.votes >= 12) {
            return 'em em-laughing'
        } else if (this.props.votes >= 9) {
            return 'em em-smiley'
        } else if (this.props.votes >= 6) {
            return 'em em-slightly_smiling_face'
        } else if (this.props.votes >= 3) {
            return 'em em-neutral_face'
        } else if (this.props.votes >= 0) {
            return 'em em-confused'
        } else {
            return 'em em-angry'
        }
    }

    render() {
        return (
            <div className='Joke'>
                <div className="Joke-buttons">
                    <i onClick={this.props.upvote} className='fas fa-arrow-up'/>
                    <span style={{borderColor: this.getColor()}} className='Joke-votes'>{this.props.votes}</span>
                    <i onClick={this.props.downvote} className='fas fa-arrow-down'/>
                </div>
                <div className="Joke-text">
                    {this.props.joke}
                </div>
                <div className="Joke-smiley">
                    <i className={`em ${this.getEmoji()}`}/>
                </div>
            </div>
        )
    }
}

export default Joke